/**
 * TimelineTask model events
 */

'use strict';

import {EventEmitter} from 'events';
import TimelineTask from './../../models/timelineTask.model';
var TimelineTaskEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
TimelineTaskEvents.setMaxListeners(0);

// Model events
var events = {
  'save': 'save',
  'remove': 'remove'
};

// Register the event emitter to the model events
for (var e in events) {
  var event = events[e];
  TimelineTask.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    TimelineTaskEvents.emit(event + ':' + doc._id, doc);
    TimelineTaskEvents.emit(event, doc);
  }
}

export default TimelineTaskEvents;
